<template>
  <div>
    <h1>攝氏轉華氏</h1>
	<h2>{{ fahrenheit }} °F</h2>
	<label>輸入溫度：<input type="text" v-model="celsius"></label>
	<br>
	<hr>
	<h1>Vue 2 filter - Demo</h1>
	<input type="text" v-model="name">
	<ul v-for="i in nameFilter">
		<li>{{ i.name }}</li>
	</ul>
  </div>
</template>

<script>
var lists = [
	{
		name: 'Bruce',
	    id: 0
	},
	{
	  	name: 'Chuck',
	    id: 1
	},
	{
	  	name: 'Jackie',
	    id: 2
	},
	{
	  	name: 'Ziv',
	    id: 3
	}
];
export default {
  data () {
    return {
    	celsius: 0,
    	name: '',
    	lists: lists
    }
  },
  computed: {
  	fahrenheit: function () {
  		return this.celsius * 9/5 + 32;
  	},
  	nameFilter: function () {
  		var _this = this;
  		return _this.lists.filter(function(user) {
  			return user.name.toLowerCase().indexOf(_this.name.toLowerCase()) !== -1;
  		});
  	}
  }
}
</script>

<style></style>



